<template>
  <div>
    <el-form ref="form" :model="formData" label-width="80px">
      <el-form-item label="标题">
        <el-input v-model="formData.title"></el-input>
      </el-form-item>
      <el-form-item label="作者">
        <el-input v-model="formData.author"></el-input>
      </el-form-item>
      <el-form-item label="新闻分类">
        <el-select v-model="formData.type" placeholder="请选择">
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
      </el-form-item>

      <el-form-item label="图片上传" class="load">
        <el-upload
          class="avatar-uploader"
          action="https://upload-z1.qiniup.com"
          :show-file-list="false"
          :on-success="upload"
          :data="{token:token}">
          <img v-if="formData.img" :src="formData.img" class="avatar">
          <i v-else class="el-icon-plus avatar-uploader-icon"></i>
        </el-upload>
      </el-form-item>
      <el-form-item>
        <editor v-model="formData.content" @getText="getText"></editor>
      </el-form-item>
      <el-form-item>
        <el-button type="success" @click="submit" v-if="$route.name=='新闻添加'">保存</el-button>
        <el-button type="success" @click="change"  v-if="$route.name=='新闻编辑'">修改并保存</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  import editor from '@/components/editor/index'


  export default {
    name: "add",
    components: {
      editor
    },
    data() {
      return {
        formData: {
          title: '',
          author: '',
          img: '',
          type: '',
          content: "",
          contentText: '',
        },
        token: '',
        value: '',
        options:[]
        // editorInit: false
      }
    },
    methods: {
      upload(res) {
        this.formData.img = "http://image.yaojunrong.com/" + res.key;
        // this.formData.headers += `<img src="${this.formData.img}"/>`
      },
      getText(val) {
        this.formData.contentText = val
      },
      getToken() {
        this.$axios.qiniuGet().then(res => {
          this.token = res.data
        })
      },
      getcategory(){
        this.$axios.post('getcategory').then(res=>{
          this.options=res.data
        })
      },
      submit(){
        this.$axios.post("addNews",this.formData).then((res)=>{
          if(res.code==200){
            this.$message({
              type: 'success',
              message: '提交成功!'
            });
            setTimeout(()=>{
              this.$router.push("/crmnews")
            },1000)
          }else{
            this.$message({
              type: 'warning',
              message: res.msg
            });
          }

        })
      },
      getdata(){
        this.$axios.post('getNews',{id: this.$route.query.id}).then(res=>{
          this.formData = res.data[0]
        })
      },
      change(){
        let params ={
          ...this.formData,
          id:this.$route.query.id
        }
        this.$axios.post("updateNews",params).then(res=>{
          if(res.code==200){
            this.$message({
              type: 'success',
              message: '修改成功!'
            });
            setTimeout(()=>{
              this.$router.push('/crmnews')
            },1000)
          }

        })
      }
    },
    created() {
      this.$nextTick(()=>{
        if(this.$route.name =="新闻编辑"){
          this.getdata()
        }
        this.getToken()
        this.getcategory()

      })

    },
    watch:{
      $route(to,from,next){
        this.formData = {
          title: '',
          author: '',
          img: '',
          content: "",
          contentText: ''
        }
      }
    }
  }
</script>
<style>
  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }

  .load .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }

  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
</style>
<style scoped>

</style>



